<template>
  <div id="bigbox">
    <div class="specification">
      <h1>运营资料下载</h1>
      <h4>上传文件名格式说明书</h4>
      <div class="line3"></div>
      <p style="padding: 0 2vw">
        上传文件名必须含有工厂和货柜号。（例如合迪工厂
        D999的文件必须包含合迪和D99）否则上传失败、失败后请关闭上传口重新打开。
      </p>
      <div class="cicle1">
        <div class="cicle2" @click="close_specification()">
          <div class="line1"></div>
          <div class="line2"></div>
        </div>
      </div>
    </div>
    <div class="leftDiv">
      <h3 style>待处理货柜信息</h3>

      <div>
        渠道
        <el-select v-model="ditch" placeholder="请选择">
          <el-option
            v-for="item in ditchList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div>
        国家
        <el-select v-model="country" placeholder="请选择">
          <el-option
            v-for="item in countryList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div>
        站点
        <el-select v-model="site" placeholder="请选择">
          <el-option
            v-for="item in siteList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
