            margin-left: 1.5vw;
            margin-top: 0.5vh;
            text-align: center;
          "
          @click="flag3 = true"
          >已回传</el-button
        >
        <el-button
          :type="flag3 ? 'info' : 'warning'"
          style="width: 4vw; text-align: center"
          @click="flag3 = false"
          >未回传</el-button
        >
      </div>
      <div class="UlDive">
        <ul class="leftUl" v-if="flag3">
          <li
