          >
            {{ item.container_num }}-{{ item.factory }}
          </li>
          <li v-if="searchList.length == 0">没有找到符合条件货柜号</li>
        </ul>
        <!-- <ol v-if="flag2">
          <li>无符合条件的货柜号</li>
        </ol>-->
        <ul class="noUl" v-if="!flag3">
          <li v-for="(item, index) in searchNoList" :key="index">
            {{ item.container_num }}
          </li>
          <li v-if="searchNoList.length == 0">没有找到符合条件货柜号</li>
        </ul>
      </div>
    </div>
    <div class="hidTable" style="flex: 7; padding-left: 2vw; margin-left: 1vw">
      <h3 style="color: white">
        <a
