                >
              </div>
            </td>
            <td>{{ tableList.box_stuck_date }}</td>
            <td>{{ tableList.box_stuck_state }}</td>
          </tr>
          <tr>
            <td>亚马逊条码</td>
            <td>
              <div v-for="(item, index) in tableList.bar_code" :key="index">
                <a
                  :href="
                    'https://www.beyoung.group/file_download/?path=' +
                      getFileUrl(item) +
                      '&filename=' +
                      getfilename(item)
                  "
                  download
                  >{{ getfilename(item) }}</a
                >
              </div>
            </td>
            <td>{{ tableList.bar_code_date }}</td>
            <td>{{ tableList.bar_code_state }}</td>
          </tr>
          <tr>
            <td>海外仓条码</td>
            <td>
              <div v-for="(item, index) in tableList.bar_code_new" :key="index">
                <a
