                >
              </div>
            </td>
            <td>{{ tableList.bar_code_date_new }}</td>
            <td>{{ tableList.bar_code_state_new }}</td>
          </tr>
        </tbody>
      </table>
      <!-- 新增 -->
      <!-- <el-button type="primary" @click="FBM_masksure"
        >海外仓建仓信息确认表</el-button
      >-->
      <!-- 结束 -->
