          action="123"
          :http-request="submitTSV"
          :on-change="beforeTSVUpload"
          :on-remove="handleRemoveTSV"
          :limit="1"
          accept="text/tab-separated-values"
          :file-list="fileListTSV"
          ref="TSVUpload"
          :auto-upload="false"
        >
