      <div class="table_FBM">
        <div v-show="SelectFBM">
          <el-table
            max-height="300"
            border
            :data="tableData"
            style="width: 100%"
          >
            <el-table-column prop="sku" label="sku" width="240">
            </el-table-column>
            <el-table-column align="center" label="是否勾选" width="120">
              <template slot-scope="scope">
                <el-checkbox v-model="scope.row.select"></el-checkbox>
              </template>
            </el-table-column>
          </el-table>
          <el-button @click="submit_SKU" type="primary">提交</el-button>
        </div>
      </div>
      <div class="edit_SKU_num" v-show="skuDialogVisible">
        <el-table
          max-height="300"
          border
          :data="tableData1"
          style="width: 100%"
        >
          <el-table-column prop="sku" label="sku" align="center" width="180">
          </el-table-column>
          <el-table-column align="center" label="数量" width="180">
            <template slot-scope="scope">
              <el-input
                v-model="scope.row.count"
                @input="scope.row.count = onlyNum(scope.row.count)"
              ></el-input>
            </template>
          </el-table-column>
        </el-table>
        <el-button
          style="margin-top: 22px"
          type="primary"
          @click="submit_sku_edit"
          >提交</el-button
        >
      </div>
    </el-dialog>-->
    <el-dialog
      title="订单分配情况"
      :visible.sync="distributionVisible"
      width="441px"
      @close="distributionVisible = false"
    >
      <el-table
        max-height="400px"
        border
        highlight-current-row
        :data="tableDataDetail"
        style="width: 100%"
      >
        <el-table-column
          align="center"
          width="300px"
          label="分配sku"
          prop="sku"
        ></el-table-column>
        <el-table-column
          align="center"
          width="100px"
          label="数量"
          prop="plan_order"
        ></el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      fileListTSV: [],
      // SelectFBM: true,
      // skuDialogVisible: false,
      // FBM_DialogVisible: false, // 弹框
      tableData: [],

      centerDialogVisible: false, //弹框

      ditch: "", //渠道选择
      site: "", //站点选择
      country: "", //国家选择
      container: "", //container输入框

      ditchList: [], //渠道下拉框数据
      siteList: [], //站点下拉框数据
      countryList: [], //国家下拉框数据

      searchList: [{ container_num: "请搜索" }], //搜索出来的可编辑数据
      searchNoList: [{ container_num: "请搜索" }], //搜索出来不可编辑数据
      tableList: {
        box_stuck: "",
        calculation_file_path: "",
        establish_warehouse: "",
        place_order: "",
        bar_code: ""
      }, //右侧表格数据

      fileData: [], //上传的文件
      file: "", // 上传的文件
      flag: 0, //判断文件变量
      flag2: false, //判断符合条件的货柜号
      flag3: true, //判断显示那部分的UL
      distributionVisible: false,
      tableDataDetail: []
    };
  },
  computed: {
    // tableData1() {
