    //     // 选中的sku
    //     if (i.select) {
    //       let obj = {};
    //       obj["sku"] = i.sku;
    //       obj["count"] = "";
    //       arr.push(obj);
    //     }
    //   });
    //   return arr;
    // }
  },
  methods: {
    showDistributionDetail() {
      this.distributionVisible = true;
      // 请求数据
      let url = this._url2 + "server/stock_management/get_distribution_sku/";
      this.$axios
        .get(url, {
          params: {
            site: this.tableList.site,
            country: this.tableList.country,
            container_num: this.tableList.container_num,
            factory: this.tableList.factory
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            this.tableDataDetail = res.data.re_sku_data || [];
          }
        });
    },
    handleRemoveTSV(file, fileList) {
      console.log(file, fileList);
    },
    checkFileType(filename) {
      return /.+\.tsv$/g.test(filename);
    },
    beforeTSVUpload(file, fileList) {
      console.log("上传之前", file, fileList);
      // 判断文件类型
      this.fileListTSV = [];
      if (!this.checkFileType(file.name)) {
        alert("请上传后缀为TSV类型的文件！");
        return;
      }
      this.fileListTSV.push(file.raw);
      console.log(file);
      this.submitTSV();
    },
    submitTSV() {
      // 发送文件请求
      console.log("123");
      let submitData = new FormData();
      submitData.append("site", this.tableList.site);
      submitData.append("country", this.tableList.country);
      submitData.append("container_num", this.tableList.container_num);
      console.log(this.fileListTSV, this.fileListTSV[0]);
      submitData.append("files", this.fileListTSV[0]);

      this.$axios
        .post(
          this._url2 + "server/stock_management/warehouse_script/",
          submitData
        )
        .then(res => {
          if (res.data.code == 200) {
            this.$message.success("文件上传成功！");
            this.fileListTSV = [];
          }
        });
    },
    onlyNum(value) {
      return value.replace(/\D/g, "");
    },
    // submit_sku_edit() {
    //   let sku = [];
    //   let num = [];
    //   for (let j = 0; j < this.tableData1.length; j++) {
    //     let i = this.tableData1[j];
    //     console.log(i);
    //     if (i.count === "0" || i.count.length === 0) {
    //       continue;
    //     }
    //     sku.push(i.sku);
    //     num.push(i.count);
    //   }

    //   console.log("提交参数", sku, num);
    //   if (sku.length === 0 || num.length === 0) return;
    //   let formData = new FormData();
    //   //  加上国家，站点 ，货柜号 tableList.container_num
    //   // this.$axios.post(url, formData).then(res => {
    //   //   if (res.data.code === 200) {
    //   //     alert("提交成功");
    //   //   } else {
    //   //     alert("提交失败！");
    //   //   }
    //   // });
    //   // 发请求
    // },
    // FBM_masksure() {
    //   this.FBM_DialogVisible = true;
    //   // 请求sku列表  参数 国家，tableList.country  站点：tableList.site
    //   axios({
    //     url: this._url2 + "server/stock_management/oversea_location_sku/",
    //     method: "get",
    //     params: {
    //       site: this.tableList.site,
    //       country: this.tableList.country
    //     }
    //   }).then(res => {
    //     console.log(res.data.data);
    //     let skuData = res.data.data || [];
    //     if (skuData.length > 0) {
    //       skuData.forEach((item, i) => {
    //         this.$set(this.tableData, i, {});
    //         let skuData = this.tableData[i];
    //         this.$set(skuData, "sku", item.sku);
    //         this.$set(skuData, "select", false);
    //       });
    //     }
    //   });
    // },
    submit_SKU() {
      this.SelectFBM = false;
      this.skuDialogVisible = true;
    },
    uploadFile() {},
    getDefaultData() {
      //获取下拉框数据
      axios({
        url: this._url2 + "server/stock_management/operating_upload",
        method: "get"
      }).then(res => {
        console.log(res);
        this.ditchList = res.data.re_list[1];
        // this.siteList = res.data.re_list[2]
        // this.countryList = res.data.re_list[0]
      });
      axios({
        //获取国家下拉框（权限用）
        url: this._url2 + "server/personnel_management/get_sign_country/",
        method: "get"
      }).then(res => {
        let demo = res.data.data;
        for (let i = 0; i < demo.length; i++) {
          let obj = {
            label: demo[i],
            value: demo[i]
          };
          this.countryList.push(obj);
        }
      });
    },
    searchContainer() {
      //搜索货柜
      axios({
        url: this._url2 + "server/stock_management/operating_upload",
        method: "get",
        params: {
          site: this.site,
          country: this.country,
          container: this.container,
          platform: this.ditch
        }
      }).then(res => {
        console.log(res);
        this.searchList = res.data.re_data;
        this.searchNoList = res.data.re_no_data;
        for (let i = 0; i < this.searchList.length; i++) {
          if (this.searchList[i].place_order) {
            if (this.searchList[i].place_order.indexOf("@") == -1) {
              this.searchList[i].place_order = [this.searchList[i].place_order];
            } else {
              this.searchList[i].place_order = this.searchList[
                i
              ].place_order.split("@");
            }
          }

          if (this.searchList[i].calculation_file_path) {
            if (this.searchList[i].calculation_file_path.indexOf("@") == -1) {
              this.searchList[i].calculation_file_path = [
                this.searchList[i].calculation_file_path
              ];
            } else {
              this.searchList[i].calculation_file_path = this.searchList[
                i
              ].calculation_file_path.split("@");
            }
          }

          if (this.searchList[i].establish_warehouse) {
            if (this.searchList[i].establish_warehouse.indexOf("@") == -1) {
              this.searchList[i].establish_warehouse = [
                this.searchList[i].establish_warehouse
              ];
            } else {
              this.searchList[i].establish_warehouse = this.searchList[
                i
              ].establish_warehouse.split("@");
            }
          }

          if (this.searchList[i].box_stuck) {
            if (this.searchList[i].box_stuck.indexOf("@") == -1) {
              this.searchList[i].box_stuck = [this.searchList[i].box_stuck];
            } else {
              this.searchList[i].box_stuck = this.searchList[i].box_stuck.split(
                "@"
              );
            }
          }

          if (this.searchList[i].bar_code) {
            if (this.searchList[i].bar_code.indexOf("@") == -1) {
              this.searchList[i].bar_code = [this.searchList[i].bar_code];
            } else {
              this.searchList[i].bar_code = this.searchList[i].bar_code.split(
                "@"
              );
            }
          }

          if (this.searchList[i].bar_code_new) {
            if (this.searchList[i].bar_code_new.indexOf("@") == -1) {
              this.searchList[i].bar_code_new = [
                this.searchList[i].bar_code_new
              ];
            } else {
              this.searchList[i].bar_code_new = this.searchList[
                i
              ].bar_code_new.split("@");
            }
          }
        }
        let leftUl = document.querySelector(".leftUl").querySelectorAll("li");
        for (let i = 0; i < leftUl.length; i++) {
          leftUl[i].style.background = "";
        }
      });
    },
    submitUploadFile() {
      //上传文件
      if (this.flag === 0) {
        // console.log(this.tableList.site,this.tableList.country,this.tableList.platform,this.tableList.container_num)
        let id = this.tableList.id;
        let formData = new FormData();
        // formData.append('file_name',this.fileData)
        for (let i = 0; i < this.fileData.length; i++) {
          formData.append("file_name", this.fileData[i]);
        }
        formData.append("site", this.tableList.site);
        formData.append("country", this.tableList.country);
        formData.append("platform", this.tableList.platform);
        formData.append("container_num", this.tableList.container_num);
        formData.append("id", this.tableList.id);
