                params: {
                  site: this.site,
                  country: this.country,
                  container: this.container,
                  platform: this.ditch
                }
              }).then(res => {
                // console.log(res)
                this.searchList = res.data.re_data;
                for (let i = 0; i < this.searchList.length; i++) {
                  if (this.searchList[i].place_order) {
                    if (this.searchList[i].place_order.indexOf("@") == -1) {
                      this.searchList[i].place_order = [
                        this.searchList[i].place_order
                      ];
                    } else {
                      this.searchList[i].place_order = this.searchList[
                        i
                      ].place_order.split("@");
                    }
                  }

                  if (this.searchList[i].calculation_file_path) {
                    if (
                      this.searchList[i].calculation_file_path.indexOf("@") ==
                      -1
                    ) {
                      this.searchList[i].calculation_file_path = [
                        this.searchList[i].calculation_file_path
                      ];
                    } else {
                      this.searchList[
                        i
                      ].calculation_file_path = this.searchList[
                        i
                      ].calculation_file_path.split("@");
                    }
                  }

                  if (this.searchList[i].establish_warehouse) {
                    if (
                      this.searchList[i].establish_warehouse.indexOf("@") == -1
                    ) {
                      this.searchList[i].establish_warehouse = [
                        this.searchList[i].establish_warehouse
                      ];
                    } else {
                      this.searchList[i].establish_warehouse = this.searchList[
                        i
                      ].establish_warehouse.split("@");
                    }
                  }

                  if (this.searchList[i].box_stuck) {
                    if (this.searchList[i].box_stuck.indexOf("@") == -1) {
                      this.searchList[i].box_stuck = [
                        this.searchList[i].box_stuck
                      ];
                    } else {
                      this.searchList[i].box_stuck = this.searchList[
                        i
                      ].box_stuck.split("@");
                    }
                  }

                  if (this.searchList[i].bar_code) {
                    if (this.searchList[i].bar_code.indexOf("@") == -1) {
                      this.searchList[i].bar_code = [
                        this.searchList[i].bar_code
                      ];
                    } else {
                      this.searchList[i].bar_code = this.searchList[
                        i
                      ].bar_code.split("@");
                    }
                  }

                  if (this.searchList[i].bar_code_new) {
                    if (this.searchList[i].bar_code_new.indexOf("@") == -1) {
                      this.searchList[i].bar_code_new = [
                        this.searchList[i].bar_code_new
                      ];
                    } else {
                      this.searchList[i].bar_code_new = this.searchList[
                        i
                      ].bar_code_new.split("@");
                    }
                  }
                }
                for (let i = 0; i < this.searchList.length; i++) {
                  if (id == this.searchList[i].id) {
                    this.tableList = this.searchList[i];
                  }
                  this.fileData = [];
                  this.file = "";
                  this.$refs.upload.clearFiles();
                }
              });
            } else if (res.data.code == 400) {
              // 如果code 400 ，标红
              this.$message({
                showClose: true,
                message: res.data.msg,
                type: "error"
              });
            }
          });
      } else {
        alert("文件格式错误重新上传");
        this.flag = 0;
        this.centerDialogVisible = false;
      }
    },
    close() {
      //关闭DIALOG事件
      this.fileData = [];
      this.file = "";
      this.$refs.upload.clearFiles();
      this.flag = 0;
    },
    handleRemove(file) {
      for (let i in this.fileData) {
        if (this.fileData[i] === file.raw) {
          this.fileData.splice(i, 1);
        }
      }
    },
    showDetail(id, index) {
      this.SelectFBM = true;
      this.skuDialogVisible = false;
      //打开右侧表格并赋值
      document.querySelector(".hidTable").style.visibility = "visible";
      let containerLi = document.querySelectorAll(".containerLi");
      for (let i = 0; i < containerLi.length; i++) {
        containerLi[i].style.background = "";
      }
      containerLi[index].style.background = "skyblue";
      for (let i = 0; i < this.searchList.length; i++) {
        if (id == this.searchList[i].id) {
          this.tableList = this.searchList[i];
          console.log(this.tableList);
        }
      }
    },
    getfilename(x) {
      //获取文件名函数
      if (!x) {
      } else {
        let pos = x.lastIndexOf("/");
        return x.substring(pos + 1);
      }
    },
    // 文件上传前
    beforeUpload(file) {
      // let num1 = this.findPosition(file.name, "-", 1) + 1;
      // let num2 = this.findPosition(file.name, "-", 2);
      // let str = file.name.substring(num1, num2);
      let x = this.tableList.container_num;
      console.log(file.name);
      let supplier = this.tableList.factory;
      // let containerLi = document.querySelectorAll(".containerLi");
      // for(let i = 0;i < containerLi.length;i++){
      //    if(containerLi[i].style.background == 'skyblue') {
      //        x = containerLi[i].innerHTML
      //    }
      // }
      if (file.name.indexOf(x) == -1 || file.name.indexOf(supplier) == -1) {
        this.flag++;
        alert("上传文件名字错误，请关闭弹框后重新上传,");
      } else {
        this.fileData.push(file);
        this.file = file;
      }
    },
    getFileUrl(url) {
      //获取文件路径
      if (url) {
        let pos = url.lastIndexOf("/");
        // let pos2 = url.indexOf("/");
        return url.substring(0, pos + 1);
      }
    },
    findPosition(str, queryStr, pon) {
      if (str) {
        //查找字符函数（校验文件）
        var x = str.indexOf(queryStr);
        for (var i = 0; i < pon; i++) {
          x = str.indexOf(queryStr, x + 1);
        }
        return x;
      }
    },
    returnnum(str) {
      if (str) {
        console.log(str);
        let pos = this.findPosition(str, "-", 2);
        let pos1 = str.indexOf("-");
        console.log(str.substring(pos1 + 1, pos));
        return str.substring(pos1 + 1, pos);
      }
    },
    close_specification() {
      document.querySelector(".specification").style.display = "none";
    },

    open_specification() {
      document.querySelector(".specification").style.display = "block";
    }
  },
  created() {
    this.getDefaultData();
    this.searchContainer();
  },
  watch: {
    searchList: function() {
      if (this.searchList.length >= 1) {
        this.flag2 = false;
      } else {
        this.flag2 = true;
      }
    },
    country: function() {
      //由国家下拉框选择动态生成站点下拉框
      this.site = "";
      axios({
        url: this._url2 + "server/personnel_management/get_sign_area/",
        method: "get",
        params: {
          country: this.country
        }
      }).then(res => {
        this.siteList = [];
        let demo = res.data.data;
        for (let i = 0; i < demo.length; i++) {
          let obj = {
            value: demo[i],
            label: demo[i]
          };
          this.siteList.push(obj);
        }
      });
    }
  }
};
</script>

<style scoped>
.BottomGroupBtn {
  display: flex;
  justify-content: center;
}
.BottomGroupBtn .el-button {
  margin-right: 20px;
  height: 30px;
}
table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 60vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}
td > span.detail {
