}

/* 说明书 */
.specification {
  display: none;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background: linear-gradient(to bottom right, #4049bf, #409fbf);
  width: 650px;
  height: 350px;
  border-radius: 15px;
  overflow: hidden;
  animation: speim 1s;
  z-index: 1;
}

.cicle1 {
  position: absolute;
  top: -80px;
  right: -80px;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  border: 1px solid GhostWhite;
}

.cicle2 {
  position: absolute;
  width: 20px;
  height: 20px;
  left: 40px;
  top: 100px;
  border-radius: 50%;
  border: 2px solid ghostwhite;
