  width: 100px;
  height: 30px;
  background: linear-gradient(to bottom right, #4049bf, #409fbf);
  line-height: 28px;
  border-radius: 15px;
  font-size: 16px;
  color: white;
  text-decoration: none;
  text-align: center;
  z-index: 0;
}

@keyframes speim {
  0% {
    width: 0;
    height: 0;
    opacity: 0;
    top: 100px;
    left: 900px;
  }
  100% {
    width: 650px;
    opacity: 1;
    top: 50%;
    left: 50%;
    height: 350px;
  }
}

.open_specification2 {
  display: inline-block;
  width: 200px;
  height: 30px;
  background: linear-gradient(to bottom right, orange, yellow);
  line-height: 28px;
  border-radius: 15px;
  font-size: 16px;
  color: white;
  text-decoration: none;
  text-align: center;
  z-index: 0;
}
</style>



